import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'


// route level code-splitting, generates chunk (about.[hash].hs) which is lazy-loaded when the route is visited.
const PrizePools = () => import(/* webpackChunkName: "pools" */ '../views/PrizePools.vue');
const Team = () => import(/* webpackChunkName: "team" */ '../views/Team.vue');
const HowItWorks = () => import(/* webpackChunkName: "howitworks" */ '../views/HowItWorks.vue');

const Protected = () => import(/* webpackChunkName: "protected" */ '../components/Protected.vue');

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      requiresAuth: false
    }
  },
  {
    path: '/prizepools',
    name: 'PrizePools',
    component: PrizePools,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/howitworks',
    name: 'HowItWorks',
    component: HowItWorks,
    meta: {
      requiresAuth: false
    }
  },
  {
    path: '/team',
    name: 'Team',
    component: Team,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/protected',
    name: 'Protected',
    component: Protected,
    meta: {
      requiresAuth: false
    }
  },
]



const router = new VueRouter({
  //mode: 'history', // FIXME: Does not work well on github.io pages
  base: process.env.BASE_URL,
  routes
})

