<template>
    <div>
    <b-modal
    ok-only ok-variant="secondary" ok-title="Cancel"
    @hide="onModalClose()"
    v-model='modalVisible' id="modal-1" title="ViteConnect Login">
    <div style="align-items:center; justify-content:center; text-align: center">
    <p class="my-2"> Scan the QR Code below with your Vite Wallet. </p>
    <div v-html="imgtag"></div>
    <p class="my-2">Vite Wallet available from <a href="https://app.vite.net">app.vite.net</a></p>
    </div>
    </b-modal>
    
    <!--<span>ViteConnect status: {{getConnectionStatus}}</span>-->
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import qrcode from 'qrcode-generator/qrcode.js'



export default {
    name: 'ViteConnectModal',
    components: {

    },
    computed: {
        ...mapGetters(['getConnectionStatus', 'getConnectionURI']),
        modalVisible: {
            get() {
                if( this.getConnectionStatus == "waiting" ) {
                    return true;
                }
                return false;
            },
            set(){
                
            }

        },/*
        modalVisible() {
                // for some reason a set function is being called.
                //https://stackoverflow.com/questions/54928111/should-we-use-v-model-to-modify-vuex-store
                // do I need get and set? seems to work ok with just a return...
                if( this.getConnectionStatus == "waiting" ) {
                    return true;
                }
                return false;
        },*/
        imgtag : function (){
            if ( this.getConnectionStatus == 'waiting') {
                const typeNumber = 0;
                const errorCorrectionLevel = 'M';
                const cellSize = 4;
                const qr = qrcode(typeNumber, errorCorrectionLevel);
                qr.addData(this.getConnectionURI);
                qr.make();
                return qr.createImgTag(cellSize);
            } else {
                return "QR Loading."
            }
        }
    },
    methods: {
        ...mapActions(['confirmViteConnectLogin', 'requestViteConnectLogout']),
        onModalClose: function() {
            if( this.getConnectionStatus == "waiting" ){
                this.requestViteConnectLogout();
            }
        }
    }
}
</script>