<template>
    <div v-if="adminStatus"> 
        Admin Status: Enabled
    </div>
</template>

<script>

export default {
    name: "AdminConsole",
    components: {
    },
    data() {
        return {
            buffer: [],
            lastKeyTime: 0,
        };
    },
    computed: {
        adminStatus(){
            return this.$store.getters.isAdmin;
        }
    },
    methods: {        
        runCommand(args) {
            //console.log(args)
            this.$store.dispatch("runCommand", args)
        },
        initKeyMapper(keystrokeDelay, callback) {
            const charList = "abcdefghijklmnopqrstuvwxyz0123456789";
            this.buffer = [];
            this.lastKeyTime = Date.now();
            document.addEventListener("keydown", (event) => {
                if (event.key === 'Enter') {
                  callback(this.buffer.join(''));
                  this.buffer = [];
                }

                const key = event.key.toLowerCase();

                // we are only interested in alphanumeric keys
                if (charList.indexOf(key) === -1) return;

                const currentTime = Date.now();

                if (currentTime - this.lastKeyTime > keystrokeDelay) {                    
                    this.buffer = [];
                }

                this.buffer.push(key);
                this.lastKeyTime = currentTime;

                
            });
        },
    },
    mounted() {
      this.initKeyMapper(1000, this.runCommand)      
    },
};
</script>
