import Vue from 'vue';
import {initConnector, destroyConnector} from '../../services/viteconnect';
import {getAccountBalance} from '../../services/viteprovider';
import BigNumber from '../../utils/bigNumber'
// some good references for improvements
// https://github.com/vitelabs/vite-web-wallet/blob/a1d12ac7c8cf4e7598dd031fc6a9bd5c145875a7/srcPC/wallet/store.js
// https://github.com/vitelabs/vite-web-wallet/blob/a1d12ac7c8cf4e7598dd031fc6a9bd5c145875a7/srcPC/wallet/vb.js

// connection status:
// disconnected
// waiting
// connected
const state = {    
    connectionStatus : "disconnected",    
    userAddress : null,
    connectionURI : null,
    vbInstance: null,
    balanceMap: {},
};

const getters = {
    getConnectionStatus : (state) => state.connectionStatus,
    getUserAddress : (state) => state.userAddress,
    getConnectionURI : (state) => state.connectionURI,
    getvbInstance : (state) => state.vbInstance,
    connected : (state) => (state.connectionStatus === 'connected'),
    getBalanceByTokenId : (state) => (tokenId) => state.balanceMap[tokenId]
};

const actions = {
    async updateBalanceMap({state, commit, getters}){
        try {
            if( getters.connected ){
                const res = await getAccountBalance(state.userAddress);
                commit('UPDATE_BALANCE_MAP', res.balance.balanceInfoMap)
            } else {
                throw new Error("Not connected.");
            }
        } catch(err) {
            console.warn("Can't update user balance: " + String(err));
        }
    },

    async requestViteConnectLogin({ commit, dispatch }){
        // user opens page, should check if session exists in text above
        // user presses deposit/withdraw and logged-in is false, or
        // user presses sign-in-new-account
        //
        // if not should create a new session, update connection status
        // to waiting for user, which in turn triggers the modal popup
        // with the updated URI/instructions. Closing the modal doesn't
        // matter, if they try to deposit they will still not be logged
        // in and that will initiate requestviteConnectlogin
        //
        // maybe initiate connection and pass confirmViteConnectLogin as callback?
        //
        const vbInstance = await initConnector({
            onConnect: (userAddress) => { dispatch('confirmViteConnectLogin', {userAddress}) },
            onDisconnect: () => { dispatch('requestViteConnectLogout') }
        });
        console.log(vbInstance);
        commit('setConnectionStatus', "waiting");
        commit('setConnectionURI', vbInstance.uri);
        commit('setvbInstance', vbInstance);
    },
    async confirmViteConnectLogin({ commit }, {userAddress}){
        // 
        commit('setConnectionStatus', "connected");
        commit('setUserAddress', userAddress);
    },
    async requestViteConnectLogout({ commit }){
        // user chooses sign-out, should delete the viteconnect session, etc.
        await destroyConnector();
        commit('setConnectionStatus', "disconnected");        
        commit('setConnectionURI', null);
        commit('setUserAddress', null);
        commit('setvbInstance', null);
    },

};

const mutations = {
    setConnectionStatus: (state, connectionStatus) => state.connectionStatus = connectionStatus,
    setUserAddress: (state, userAddress) => state.userAddress = userAddress,
    setConnectionURI: (state, connectionURI) => state.connectionURI = connectionURI,
    setvbInstance: (state, vbInstance) => state.vbInstance = vbInstance,
    UPDATE_BALANCE_MAP(state, balanceInfoMap){
        for( let key in balanceInfoMap ){
            Vue.set(state.balanceMap, String(key), new BigNumber.BigNumber(balanceInfoMap[key].balance) )
        }
    }
};

export default {
    state,
    getters,
    actions,
    mutations
}