import store from '../../store';
import {abi, accountBlock} from '@vite/vitejs'
import {getViteClient} from '../../services/viteprovider'
import CONTRACT from '../../contracts/PrizePoolContract.js'


// Comments on hacky fix:
// Some recent changes to vite.js made encoding/decoding work differently. All uses of 
// the encoding/decoding functions may be bad practice, though they work.
// The following "FRAGMENT" is used because our ABI spec does not seem to be
// compatiable with some vite.js functions and until we fix that, this is needed.
const WINNER_EVENT_FRAGMENT = CONTRACT.abi.find(x=>{ return x.name == "Winner"})
const WINNER_EVENT_ID  = abi.encodeLogSignature(WINNER_EVENT_FRAGMENT, "Winner");
// The correct approach should simply be:
//const WINNER_EVENT_ID  = abi.encodeLogSignature(CONTRACT.abi, "Winner");


////////////////////////////////////////////////////////////
//                 Account required:
////////////////////////////////////////////////////////////

async function sendVcTx(...args) {    
    return store.getters.getvbInstance.sendCustomRequest({method: 'vite_signAndSendTx', params: args}
    ).then(signedBlock => {console.log(signedBlock); return signedBlock}, err => console.error(err))
    .catch( (err) => {console.warn(err); throw(err)});
}

async function callContract(contractAddress, methodName, params, amount){
    let block = await accountBlock.createAccountBlock('callContract',{
        address: store.getters.getUserAddress,
        abi: CONTRACT.abi,
        toAddress: contractAddress,
        params,
        methodName,            
        amount: String(amount)
    });    
    block = block.accountBlock
    return sendVcTx({block, abi: CONTRACT.abi})
}

export async function callDeposit(contractAddress, amount) {
    return callContract(contractAddress, "deposit", [], amount);
}

export async function callWithdraw(contractAddress, amount) {
    return callContract(contractAddress, "withdraw", [amount], 0);
}    

export async function callDraw(contractAddress) {
    return callContract(contractAddress, "draw", [], 0);
}

export async function donateToContract(contractAddress, amount){
    return sendTx(contractAddress, amount)// 10 VITE
}


export async function sponsorContract(contractAddress, amount){
    return callContract(contractAddress, "sponsorDeposit", [], amount);
}



export async function testingSmallDeposit(contractAddress){
    return sendTx(contractAddress, "10000000000000000000")// 10 VITE
}

async function sendTx(address, amount) {
    let block = accountBlock.createAccountBlock('send', {
        address: store.getters.getUserAddress,
        toAddress: address,
        amount
    })//.setProvider(provider).setPrivateKey(account.privateKey);
    block = block.accountBlock
    return sendVcTx({block})
}

////////////////////////////////////////////////////////////
//                  Provider required:
////////////////////////////////////////////////////////////

async function callOffChain(contractAddress, methodName, params){
    const calldataHex = abi.encodeFunctionCall(CONTRACT.abi,params,methodName);
    const calldataBase64 = Buffer.from(calldataHex, 'hex').toString('base64');
    const code = Buffer.from(CONTRACT.offChain, 'hex').toString('base64');
    
    const viteClient = await getViteClient();
    const res = await viteClient.request('contract_callOffChainMethod', {
        address: contractAddress,
        code,
        data: calldataBase64
    }).then((result) => {
        return result;
    }).catch((err) => {        
        console.warn(err);
    });
    const responseDataHex = Buffer.from(res, 'base64').toString('hex');
    const methodABI = CONTRACT.abi.find(x=>x.name===methodName).outputs
    const out = abi.decodeParameters(methodABI, responseDataHex);
    const zipped = {};
    for ( let i=0; i<methodABI.length; i++){
        zipped[methodABI[i].name] = out[i]
    }
    return zipped;
}

export async function getPoolInfo(contractAddress){
    return callOffChain(contractAddress, "poolInfo", []);
}

export async function getUserInfo(contractAddress, address){
    return callOffChain(contractAddress, "userInfo", [address]);
}

export async function getSponsorInfo(contractAddress, address){
    return callOffChain(contractAddress, "sponsorInfo", [address]);
}

export async function getVote(contractAddress) {
    return callOffChain(contractAddress, "getVote", []);
}


export async function getWinners(contractAddress) {

    const viteClient = await getViteClient();

    const toHeight = (await viteClient.request("ledger_getLatestAccountBlock", contractAddress)).height

    const logs = await viteClient.request(
        "ledger_getVmLogsByFilter", {
            addressHeightRange: {
                [contractAddress]: {
                    fromHeight: "1",
                    toHeight  : toHeight
                }
            }
        }
    );

    const poolEvents = [];
    let d = Date();

    for (let i = 0; i < logs.length; i++) {
        const log = logs[i];

        //console.log("Processing logs:", i, "/" , logs.length)

        if (log.vmlog.topics.includes(WINNER_EVENT_ID)) {

            // Hacky fix for decodeLog:
            // See top comment as well. All ABI decode/encoding may be bad practice, but it works.
            // For the following we use the "FRAGMENT" from above, as well as an empty string for the data since
            // log.vmlog.data is null, but decodeLog does not handle null properly.
            let data = abi.decodeLog(WINNER_EVENT_FRAGMENT, "", log.vmlog.topics, "Winner");
            // One of the following is the approach that SHOULD work, but doesn't [not sure about the .slice(1)]:
            //let data = abi.decodeLog(CONTRACT.abi, log.vmlog.data, log.vmlog.topics.slice(1), "Winner");
            //let data = abi.decodeLog(CONTRACT.abi, log.vmlog.data, log.vmlog.topics, "Winner");

            // get snapshot height for selected logs
            const ablock = await viteClient.request("ledger_getAccountBlockByHash",
                log.accountBlockHash
            );

            d = new Date( ablock.timestamp * 1000 );

            poolEvents.push(
                {
                    event:    "Winner",
                    address:  data.winner,
                    amount:   data.amount / 1e18,
                    tx:       log.accountBlockHash,
                    snapshot: ablock.firstSnapshotHeight,
                    timeUTC:  ablock.timestamp,
                    time:     d
                }
            );
        }
    }

    return poolEvents;



}
