import provider from '@vite/vitejs-ws'
import { ViteAPI } from '@vite/vitejs';
//import { providerURL } from '../constants';

// Setup ViteAPI client using websockets. We will use this to subscribe to events.
//const providerURL = 'wss://buidl.vite.net/gvite/ws'; // testnet node
//const fallbackProviderURL = 'wss://node-vite.thomiz.dev/wss'; // 'wss://vitestats.com/ws';
const providerURL = 'wss://node.vite.net/gvite/ws'; // mainnet
//const providerURL = process.env.VUE_APP_providerURL;
const providerTimeout = 60000;
const providerOptions = { retryTimes: 10, retryInterval: 10000 };


const WS_RPC = new provider(providerURL, providerTimeout, providerOptions);
const viteClient = new ViteAPI(WS_RPC, () => {
    console.log("client connected");
    console.log(WS_RPC.path)
});

export async function getViteClient(){
    return viteClient;
}

export function getSnapshotChainHeight() {
    return viteClient.request('ledger_getSnapshotChainHeight');
}

export function getAccountBlockByHash(hash) {
    return viteClient.request('ledger_getAccountBlockByHash', hash);
}

export function getAccountBalance(address) {
    return viteClient.getBalanceInfo(address);
}

