<template>
    <b-navbar toggleable="lg" type="dark" variant="primary">
    <b-navbar-brand to="/">
    <img src="@/assets/vite-together-logo-blue32x32.png" class="rounded" style="background-color:white">
    ViteTogether
    </b-navbar-brand>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav>
        <b-nav-item active-class="active" router-link-active to="/prizepools"><b-icon-gift/>&nbsp;Prize Pools</b-nav-item>
        <!--<b-nav-item disabled active-class="active" router-link-active to="/savingspools"
        v-b-tooltip.hover title="Coming soon!"><b-icon-cash-stack/>&nbsp;Savings Pools</b-nav-item>-->
        <b-nav-item active-class="active" router-link-active href="https://app.vivafi.org" target="_blank"><b-icon-hammer/>&nbsp;Staking Pools</b-nav-item>
        <b-nav-item active-class="active" router-link-active to="/howitworks">How it works</b-nav-item>
        <!--<b-nav-item active-class="active" router-link-active to="/sponsor">Sponsor a pool</b-nav-item>-->
        <!--<b-nav-item active-class="active" router-link-active to="/team">The Team</b-nav-item>-->
      </b-navbar-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">

        <b-nav-item id="tooltip-1"
        :disabled='!(getConnectionStatus == "connected")'
        v-on:click="doCopyAddress"
        >
        {{getConnectionStatus == "connected" ? truncateAddress(getUserAddress) : "(no wallet connected)"}}        
        </b-nav-item>

<!--p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 200px;
}-->


        <b-nav-item-dropdown right>
          <!-- Using 'button-content' slot -->
          <template #button-content>
            ViteConnect
          </template>
          <b-dropdown-item 
          v-if="getConnectionStatus != 'connected'"
          v-on:click="requestViteConnectLogin()">Login</b-dropdown-item>
          <b-dropdown-item
          v-if="getConnectionStatus == 'connected'"
          v-on:click="requestViteConnectLogout()">Sign Out</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    name: "NavigationBar",
    computed: {
        ...mapGetters(['getConnectionStatus', 'getUserAddress'])
    },
    methods: {
        ...mapActions(['requestViteConnectLogin', 'requestViteConnectLogout']),
        doCopyAddress(){
          navigator.clipboard.writeText(this.getUserAddress);
        },
        truncateAddress(str){
          if (str.length > 26) {
          return str.substr(0, 13) + '...' + str.substr(str.length-8, str.length);
        }
    }
  }
}
</script>

<style scoped>
.active{
    color: red
}
</style>