import Connector from '@vite/connector';

// Setup ViteConnect to allow user to securely fund and guess answers.
const BRIDGE = 'wss://biforst.vite.net'
let vbInstance = null;

// initiates ViteConnect with connect/disconnect callbacks, returns connection uri.
async function initConnector({onConnect, onDisconnect}){
        //return Promise( (resolve, reject) => {
        vbInstance = new Connector({ bridge: BRIDGE })    
        await vbInstance.createSession()/*.then(() => {
            // vbInstance.uri can be turn to an QR code image.
            // Then scan the QR code image with Vite App.
            console.log('connect uri', vbInstance.uri);
        });
        console.log('connect uri2', vbInstance.uri);*/
    
        //vbInstance = new Connector({ bridge: BRIDGE })
        //bInstance.createSession()

        vbInstance.on('connect', (err, payload) => {
            const { accounts } = payload.params[0];
            if (!accounts || !accounts[0]){
                throw new Error('address is null:' + String(err));
            }
            const address = accounts[0];            
            //onConnect();
            onConnect(address);
        })    

        vbInstance.on('disconnect', () => {
            //console.log(err); // err was first argument
            onDisconnect();
            vbInstance.destroy();
        })    

        //return vbInstance.uri;
        return vbInstance;
}

async function destroyConnector(){
    vbInstance.destroy();
}

