const actions = {    

    async addPrizePool({state, commit, dispatch},{contractAddress, title}){
        try{
            if( state.poolList.includes(contractAddress) ){
                return
                //throw new Error("Contract already added.")
            }
            // FIXME: check for titles
            /////////////////////////////
            // FIXME: validate address //
            /////////////////////////////
            
            commit('ADD_PRIZE_POOL', {contractAddress, title});
            dispatch('updatePrizePool', contractAddress);
        }catch(err){
           console.warn(err);
        }
    },

    async updatePrizePool({state, commit},contractAddress){
        try{
            if( !state.poolList.includes(contractAddress) ){                
                throw new Error("Contract not added.")
            }
            const poolInfo = await getPoolInfo(contractAddress);            
            commit("UPDATE_PRIZE_POOL", {contractAddress, poolInfo});
        }catch(err){
           console.warn(err);
        }
    },

    async updatePrizePoolUserInfo({rootState, commit}, contractAddress){
        if( !contractAddress || !rootState.vcstore.userAddress ){
            return;
        }        

        if(rootState.vcstore.connectionStatus === 'connected'){            
            const userAddress = rootState.vcstore.userAddress;
            getUserInfo(contractAddress, userAddress).then((res)=>{
                commit('UPDATE_PRIZE_POOL_USER_INFO',{contractAddress, res});
            })
        }else{
            // not connected, reset info
            commit('UPDATE_PRIZE_POOL_USER_INFO', {contractAddress, res:{depositBalance: 0, lastDepositTime: 0}})
        }
    },

    async updatePrizePoolOwnerInfo({state, commit}, contractAddress){
        if( !contractAddress || !state.pools[contractAddress].owner ){
            return;
        }
        const ownerAddress = state.pools[contractAddress].owner;
        //console.log("addr:", ownerAddress)
        getSponsorInfo(contractAddress, ownerAddress).then((res)=>{
            commit('UPDATE_PRIZE_POOL_OWNER_INFO', {contractAddress, res});
        })
    },

    async setActivePool({commit}, contractAddress){
        commit("SET_ACTIVE_POOL", contractAddress);
    }


};

const getters = {

    getActivePool(state) {
        return state.activePool && state.pools[state.activePool] || ''
    }
    /*
    // If I want to do a getter approach
    https://gist.github.com/jhkueh/93be71f388b97eddd3fa23ea9f170fe9
        getPrizePoolByAddress: (state) => (address) => {
            return state.pools.filter( pool => pool.parent_id == id );
        },
        */
       // also could have getters call a function to update? maybe...
};

const mutations = {
    SET_ACTIVE_POOL(state, contractAddress){
        // don't need vue.set.
        Vue.set(state, 'activePool', contractAddress)
    },
    ADD_PRIZE_POOL(state, {contractAddress, title}){

        state.poolList.push(contractAddress);
        Vue.set(state.pools, contractAddress, {});
        Vue.set(state.pools[contractAddress], 'title', title);
        Vue.set(state.pools[contractAddress], 'contractAddress', contractAddress);
        ///////////////////////////////////////////
        // FIXME: tokenId comes from contract!   //
        ///////////////////////////////////////////
        Vue.set(state.pools[contractAddress], 'tokenId', "tti_5649544520544f4b454e6e40");
    },
    UPDATE_PRIZE_POOL_USER_INFO(state, {contractAddress, res}){
        Vue.set(state.pools[contractAddress],'userDepositBalance', new BigNumber.BigNumber(res['depositBalance']) )
        Vue.set(state.pools[contractAddress],'userLastDepositTime', new BigNumber.BigNumber(res['lastDepositTime']) )
    },
    UPDATE_PRIZE_POOL_OWNER_INFO(state, {contractAddress, res}){
        Vue.set(state.pools[contractAddress],'ownerDepositBalance', new BigNumber.BigNumber(res['depositBalance']) )
    },
    UPDATE_PRIZE_POOL(state, {contractAddress, poolInfo}){
        // Simple parsing of ABI to import/cast poolInfo data.
        // typemap { variableName : variableType, ... }
        //console.log(contractAddress, poolInfo)
        const typemap = CONTRACT.abi.find(item => item.name==="poolInfo").outputs.reduce((o, i) => ({...o, [i.name]: i.type}), {})
        try{

        for ( let key in poolInfo ){
            if ( typemap[key] === "uint256" ){
                const num = new BigNumber.BigNumber(poolInfo[key]);
                Vue.set(state.pools[contractAddress], key, num)
            } else if ( typemap[key] === "address" ){
                const str = poolInfo[key];
                Vue.set(state.pools[contractAddress], key, str);
            } else if ( typemap[key] === "string" ) {
                const str = poolInfo[key]                
                Vue.set(state.pools[contractAddress], key, str);
            } else if ( typemap[key] === "tokenId" ) {
                const str = poolInfo[key]                
                Vue.set(state.pools[contractAddress], key, str);
            } else {
                throw Error("Unknown type in poolInfo: " + String(typemap[key]));
            }
        }

        }catch(err){
            console.warn(err)
        }
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}