//import Vue from 'vue';

const state = {    
    admin: false
};

const getters = {
    isAdmin : (state) => state.admin,
};

const actions = {
    async runCommand({commit}, command){
        if( command === 'admin'){
            commit('SET_ADMIN', true)
        }
        if( command === 'noadmin'){
            commit('SET_ADMIN', false)
        }
    },

};

const mutations = {
    SET_ADMIN: (state, val) => state.admin = val
};

export default {
    state,
    getters,
    actions,
    mutations
}